@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@500&display=swap);
:root {
  --first-background-color: #f0d9d9;
  --first-background-hover-color: #cfbba3;
  --first-background-active-color: #e5d4c0;
  --second-background-color: #FFF6EA;
  --first-main-color: #e4d3bc;
  --second-main-color: #bda78e;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  position: relative;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Comfortaa', cursive;
  background-color: #f0d9d9;
  background-color: var(--first-background-color);
  color: #bda78e;
  color: var(--second-main-color);
}
